.treetable>table{--mdb-table-bg: transparent;border-collapse:collapse}.treetable{--mdb-treetable-icon-color: var(--mdb-body-color)}.treetable tr.hidden td div{max-height:0px;-webkit-box-sizing:border-box;box-sizing:border-box;padding-top:0;padding-bottom:0;overflow:hidden}.treetable tr.hidden td{padding-top:0;padding-bottom:0;-webkit-transition:padding 1s ease;transition:padding 1s ease;border:0}.treetable tr.hidden{border:0}.treetable tr.hidden button{display:none}.treetable tr div{max-height:100px;-webkit-transition:max-height .6s ease,padding .6s ease;transition:max-height .6s ease,padding .6s ease}.treetable td i{color:var(--mdb-treetable-icon-color)}